.auth{
    width: 100%;
   min-height: 100vh;
    background: rgb(7, 116, 122);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 3rem 0;
}
.form{
    width: 50%;
    height: 33rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: rgb(3, 56, 59);
    border-radius: 5px;
}
.auth_input{
    width: 70%;
    outline: none;
    height: 4rem;
    border: none;
    border-bottom: 1px solid #fff;
    border-radius: 6px;
    background: rgb(3, 56, 59);
    color:#fff;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.auth_input:focus{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(7, 116, 122);
}
.sineup{
    padding: .5rem 1rem;
    font-size: 2.3rem;
    border-radius: 6px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    background: rgb(3, 56, 59);
    box-shadow: 0 .4rem .6rem #111;
    outline: none;
    color: rgb(187, 184, 184);
    border: 1px solid rgb(7, 116, 122);
}
.sineup:disabled{
    cursor: not-allowed;
    background: rgb(128, 125, 125);
    box-shadow: none;
}
.sineup:disabled:no-button
.sineup:hover{
    background: rgb(8, 114, 120)
}
.sineup:active{
    border: 1px solid rgb(4, 52, 54)
}
.valid{
    width: 70%;
    outline: none;
    height: 4rem;
    border: none;
    border-bottom: 1px solid #fff;
    border-radius: 6px;
    background: rgb(59, 3, 33);
    color:#fff;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.valid:focus{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(226, 24, 51);
}




@media (max-width:900px){
    
    .form{
        width:80%;
        height: 30rem;
    }
}

@media (max-width:550px){
   
    .form{
        width:80%;
        height: 30rem;
        margin: 4rem 0;
    }
    .sinein{
        margin-bottom: 2rem;
    }
    .sineup{
        margin-bottom: 2rem;
    }
}